
import Vue from "vue";
import DatePicker from "@/components/form-components/DatePicker.vue";
import FileUploader from "@/components/form-components/FileUploader.vue";
import store from "@/store";
import LiveSearch from "@/components/form-components/LiveSearch.vue";
import API from "@/api/API";
import ProductsPicker from "../components/ProductsPicker.vue";
import Print from "../components/Print.vue";
import Services from "../components/Services.vue";
import CreateBeneficiary from "@/modules/orders/components/CreateBeneficiary.vue";
import {
  exactRule,
  maxLengthRule,
  minLengthRule,
  requiredRule,
  minNumberRule,
  maxNumberRule,
  minCountRule,
  emailRule
} from "@/services/helpers/validation-rules";
import { mask } from "vue-the-mask";

export default Vue.extend({
  name: "Create",

  components: {
    Services,
    ProductsPicker,
    LiveSearch,
    FileUploader,
    DatePicker,
    CreateBeneficiary,
    Print
  },

  directives: { mask },

  data: () => ({
    exactRule,
    maxLengthRule,
    minLengthRule,
    requiredRule,
    minNumberRule,
    maxNumberRule,
    minCountRule,
    emailRule,
    selectedClient: null as null | SelectComponentInterface,
    selectedCard: null as null | SelectComponentInterface,
    model: {
      documents: [] as Array<any>,
      goods: [] as Array<any>,
      countries: [] as Array<any>,
      order: {
        exchange_rate: "1.0000",
        outputs: "1",
        source: "operator",
        client: { type: "" } as any,
        document_valid_from: new Date().toISOString().substr(0, 10),
        for_cci_exposition: false as boolean,
        guaranty_required: false as boolean
      } as any
    } as any,
    allDelegatesText: {
      ro: "Toate persoanele împuternicite",
      en: "All authorized persons",
      ru: "Все уполномоченные лица"
    } as any,
    errorMessages: {} as any,
    selects: {
      cardTypes: [] as Array<any>,
      managers: [] as Array<any>,
      countries: [] as Array<any>,
      purpose: [] as Array<any>,
      packingCategories: [] as Array<any>,
      transports: [] as Array<any>,
      currencies: [] as Array<any>,
      units: [] as Array<any>,
      languages: [] as Array<any>,
      client_types: [] as Array<any>,
      release_types: [] as Array<any>,
      services: [] as Array<any>
    } as any,
    lang: store.getters["localization/getCurrent"],
    allDelegates: false as boolean,
    currency: {} as any,
    selectedTab: 2,
    totalSum: 0 as number,
    isShowCountries: {
      destination: false as boolean,
      transit: false as boolean
    } as any,
    services: {} as any,
    createBeneficiaryModal: false as boolean,
    isShowDocuments: true as boolean,
    isShowDocumentsNumber: false as boolean,
    isLoading: false,
    extra_documents: [] as Array<any>,
    extraDocument: null as any,
    breadcrumb: [
      {
        text: "orders.title",
        disabled: false,
        href: "/orders"
      },
      {
        text: "orders.form.header.create",
        disabled: true,
        href: `/orders/edit`
      }
    ]
  }),

  watch: {
    "model.order.language"() {
      if (this.allDelegates) {
        this.model.order.client_delegate = this.allDelegatesText[
          this.model.order.language
        ];
      }
    },
    goodsSum() {
      if (this.goodsSum) {
        this.model.details.required_guaranty_sum = Math.max(
          Math.trunc(0.3 * this.goodsSum),
          0
        );
      }
    }
  },

  computed: {
    computedRules(): object {
      return {
        exact: this.exactRule.bind(this),
        min: this.minLengthRule.bind(this),
        max: this.maxLengthRule.bind(this),
        required: this.requiredRule.bind(this),
        minNumber: this.minNumberRule.bind(this),
        maxNumber: this.maxNumberRule.bind(this),
        minCountRule: this.minCountRule.bind(this),
        email: this.emailRule.bind(this)
      };
    },
    destinationCountries() {
      return this.model.countries.filter(
        (item: any) => item.scope == "destination"
      );
    },
    transitedCountries() {
      return this.model.countries.filter(
        (item: any) => item.scope == "transit"
      );
    },
    formattedDate() {
      return this.$moment(this.model.order.document_valid_from, "YYYY-MM-DD")
        .add(1, "years")
        .subtract(1, "days")
        .format("DD.MM.YYYY");
    },
    goodsSum() {
      return this.model.details.countries.reduce(
        (
          total: number,
          { quantity, price }: { quantity: number; price: number }
        ) => {
          return total + Number(quantity) * Number(price);
        },
        0
      );
    },
    parentLanguage() {
      return this.model.order.language === "ro";
    },
    extraDocuments() {
      return this.model.documents.filter((item: any) =>
        item.file_type.startsWith("extra_docs")
      );
    },
    user() {
      return store.getters["authentication/credentials"].user;
    }
  },

  async beforeRouteEnter(to, from, next) {
    try {
      const user = store.getters["authentication/credentials"].user;
      const headers = {
        "x-localization": this.language
      };
      const response = await Promise.all([
        API.currency().getList(),
        API.transportTypes().getList(headers),
        API.employees().getListByService(1, headers),
        API.purposeOfUse().getList(1, headers),
        API.countries().getAcceptAta(),
        API.services().getListByServiceType(1),
        API.orders().getLanguages(),
        API.orders().getOrderReleaseModes(),
        API.clients().getClientTypes(),
        API.orders().getCardTypes(1),
        API.measureUnits().getList(1),
        API.packingCategories().getList(headers),
        API.users().getClient(user.id)
      ]);

      next(vm => {
        vm.setServerResponse(response);

        vm.$nextTick(() => {
          vm.fullLoaded = true;
        });
      });
    } catch (e) {
      await store.dispatch("alert/showError", e.message);
    }
  },

  methods: {
    isCheckedCountry(country: any, scope: string): boolean {
      return this.model.countries?.some(
        (item: any) => item.country_id === country.value && item.scope === scope
      );
    },
    async submit(): Promise<void> {
      this.isLoading = true;
      const form = this.$refs.form as Vue;
      try {
        if (form.validate()) {
          const model = { ...this.model };

          if (this.parentLanguage) {
            for (const key in model.details.countries) {
              model.details.countries[key].name_ro =
                model.details.countries[key].name;
            }
          }

          if (!model.details.guaranty_required) {
            model.details.required_guaranty_sum = 0;
          }

          model.order.document_valid_until = this.$moment(
            this.model.order.document_valid_from,
            "YYYY-MM-DD"
          )
            .add(1, "years")
            .subtract(1, "days")
            .format("YYYY-MM-DD");
          model.order.client_id = this.user.id;

          await this.$API.orders().createByClient(model);
          form.updateGuard();

          await this.$router.push("/orders");
          await this.$store.dispatch(
            "alert/showSuccess",
            this.$t("global-alert.successful_create")
          );
        } else {
          this.selectedTab = 0;
          await setTimeout(() => {
            const firstErrorInput = form.inputs.find(
              (item: any) => item.messagesToDisplay.length
            );
            this.$vuetify.goTo(firstErrorInput.$el, {
              offset: 150
            });
          }, 500);
        }
      } catch (e) {
        if (e.hasOwnProperty("errors")) {
          this.errorMessages = e.errors;
          if (e.errors.length === 1) {
            if (
              e.errors.hasOwnProperty("order") &&
              e.errors.order.hasOwnProperty("required_guaranty_sum")
            ) {
              this.selectedTab = 1;
            }
          } else if (e.errors.length) {
            this.selectedTab = 0;
          }

          await setTimeout(() => {
            const firstErrorInput = form.inputs.find(
              (item: any) => item.messagesToDisplay.length
            );
            this.$vuetify.goTo(firstErrorInput.$el, {
              offset: 150
            });
          }, 500);

          setTimeout(() => {
            this.errorMessages = {};
          }, 4000);
        }
        await this.$store.dispatch("alert/showError", e.message || e);
      }
      this.isLoading = false;
    },
    async setServerResponse(response: Array<any>) {
      const [
        currencies,
        transports,
        managers,
        purpose,
        countries,
        services,
        languages,
        releaseModes,
        clientTypes,
        cardTypes,
        units,
        packingCategories,
        user
      ] = response;

      this.selects.currencies = currencies.items;

      this.selects.transports = transports.items;
      this.model.details.transport_category_id = transports.default;

      this.selects.managers = managers.items;
      this.model.details.employee_id = managers.default;
      this.selects.purpose = purpose;
      this.selects.countries = countries;
      this.selects.services = services;

      for (const item of services) {
        this.services[item.alias] = item;
      }
      this.selects.languages = languages.items;
      this.selects.release_types = releaseModes.items;
      this.selects.client_types = clientTypes;
      this.selects.cardTypes = cardTypes.items;
      this.selects.units = units;
      this.selects.packingCategories = packingCategories;
      this.model.order.client = user;
      this.model.order.client_id = user.id;
      this.model.details.employee_id = user.id;
      this.selects.packingCategories = packingCategories;

      this.$nextTick(() => {
        this.currency = currencies.items.find(
          (item: any) => item.text === currencies.default
        );
        this.getExchangeRate(this.currency);
      });

      if (managers.length === 1) {
        const [manager] = managers;
        this.model.details.employee_id = (manager as any).value;
      }
    },
    addCountry(country: any, scope: string) {
      if (
        this.model.countries.some(
          (item: any) =>
            item.country_id === country.value && item.scope === scope
        )
      ) {
        const countryIndex = this.model.countries.findIndex(
          (item: any) =>
            item.country_id === country.value && item.scope === scope
        );
        if (countryIndex !== -1) {
          this.model.countries.splice(countryIndex, 1);
        }
      } else {
        this.model.countries.push({ country_id: country.value, scope });
      }
    },
    uploadFile(event: any, fileType: string) {
      const document = this.model.documents.find(
        (item: any) => item.file_type === fileType
      );

      if (document) {
        document.file = event;
      } else {
        this.model.documents.push({ file_type: fileType, file: event });
      }
      this.extraDocument = {};
    },
    setDocumentNumber(event: any) {
      const document = this.model.documents.find(
        (item: any) => item.file_type === "confirm_represent_power"
      );

      if (document) {
        document.number = event;
      } else {
        this.model.documents.push({
          number: event,
          file: null,
          date: null,
          file_type: "confirm_represent_power"
        });
      }
    },
    setDocumentDate(event: any) {
      const document = this.model.documents.find(
        (item: any) => item.file_type === "confirm_represent_power"
      );

      if (document) {
        document.date = event;
      } else {
        this.model.documents.push({
          number: null,
          file: null,
          date: null,
          file_type: "confirm_represent_power"
        });
      }
    },
    async search(value: string, key: string): Promise<any> {
      try {
        return await (this.$API as any)[key]().liveSearch(value);
      } catch (e) {
        await this.$store.dispatch("alert/showError");
      }
    },
    async searchClient(value: string, key: string): Promise<any> {
      try {
        return {
          items: await (this.$API as any)[key]().liveSearchClient(value)
        };
      } catch (e) {
        await this.$store.dispatch("alert/showError");
      }
    },
    async getExchangeRate(item: any): Promise<void> {
      try {
        this.model.details.currency_id = item.value;
        this.currency = item;

        if (item.text === "MDL") {
          this.model.details.exchange_rate = "1.0000";
          return;
        }

        const response = await this.$API
          .exchangeRates()
          .getExchangeRate(item.value);

        if (response) {
          this.model.details.exchange_rate = response.value;
        }
      } catch (e) {
        this.model.details.exchange_rate = 1;
        await this.$store.dispatch(
          "alert/showError",
          this.$t("orders.form.fields.no_rate")
        );
      }
    },
    async setClient() {
      try {
        if (this.selectedClient?.value) {
          this.model.order.client = await this.$API
            .clients()
            .getForEdit(Number(this.selectedClient.value));
          this.model.order.client_id = this.model.order.client.id;
        }
      } catch (e) {
        await this.$store.dispatch("alert/showError");
      }
    },
    clearClient() {
      this.model.order.client = { type: "juridical" };
    },
    changeData(items: Array<any>) {
      this.model.details.countries = items;
    },
    changeDelegate() {
      if (this.allDelegates) {
        this.model.order.client_delegate = this.allDelegatesText[
          this.model.order.language
        ];
      } else {
        this.model.order.client_delegate = "";
      }
    },
    setParentCard(card: any) {
      if (card?.value) {
        this.model.order.parent_carnet_id = card.value;
      }
    },
    getOrderSum(event: any) {
      this.totalSum = event;
    },
    getPayments(event: any) {
      this.model.payments = event;
    },
    toggleCountries(type: string): void {
      this.isShowCountries[type] = !this.isShowCountries[type];
    },
    setPurposeDescription(event: any) {
      this.model.details.purpose_id = event.value;
      if (event?.hasOwnProperty("description")) {
        this.model.details.purpose_description = event.description;
      }
      this.$forceUpdate();
    },
    getServices(services: Array<any>) {
      this.model.details.services = services;
    },
    removeDocument(document: any) {
      const documentIndex = this.model.documents.findIndex(
        (item: any) => item.file_type === document.file_type
      );

      if (documentIndex !== -1) {
        this.model.documents.splice(documentIndex, 1);
      }
    },
    setBeneficiary(event: any) {
      this.selectedClient = { value: event.id, text: event.name };
      this.setClient();
    }
  }
});
